// import React, { useState } from "react";
import { Form ,Input,message } from "antd";
import "../resources/global.css";
import { Link } from "react-router-dom";
import axios from 'axios';
import { useDispatch } from "react-redux";
import { ShowLoading,HideLoading } from "../redux/alertsSlice";

function Register() {

  const validateRegister = (_, value) => {
    // Validate the email using a regular expression
    // const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const emailRegex = /^[^\s@]+@[a-zA-Z0-9.]+(?:\.[a-zA-Z]{2,})+$/;
    const isValid = emailRegex.test(value);

    if (!isValid) {
      return Promise.reject('please check email input field');
    }

    // Extract the domain part after @ symbol
    const [, domain] = value.split('@');

    // Check if there are only 2 or 3 characters after the first dot in the domain
    const domainRegex = /^[^.]+\.([^.]{2,3})$/;
    const isCorrectFormat = domainRegex.test(domain);

    if (!isCorrectFormat) {
      return Promise.reject('Invalid email format');
    }

    return Promise.resolve();
  };
 
  // const navigate = useNavigate();
  const dispatch = useDispatch();
  const [form] = Form.useForm(); // Use Ant Design useForm hook
  const onFinish= async(values)=>{
   try {
    dispatch(ShowLoading());

    // end point
    const response = await axios.post("/api/users/register",values);
    dispatch(HideLoading());

    if(response.data.success){
      form.resetFields(); // Reset the form fields on success
      // navigate("/login")
      
      message.success(response.data.message)
    }else{
      message.error(response.data.message);
    }
   } catch (error) {
    dispatch(HideLoading());
    message.error(error.data.message);
   }
  }
 
  return (
    <div className="h-screen flex justify-center items-center">
      <div className="card w-400 p-4">
        <h1 className="text-xl text-center">Register</h1>
        <hr />
        <div className="p-3">
          <Form form={form}  layout="vertical" onFinish={onFinish}>
            {/* for every parent element v use Form.Item based on antd docs.name=Name in antd work like prop */}
            <Form.Item label="Name" name='name' 
            rules={[
             {
              required:true,
              message:"Please enter your name",
             },
             {whitespace:true},
             {min: 4}
              ]} hasFeedback>
              <Input />
            </Form.Item>
            <Form.Item label="Email" name='email' 
            rules={[
             {
              required:true,
              message:"",
             },
             {validator:validateRegister}

              ]} hasFeedback>
              <Input />
            </Form.Item>
            <Form.Item label="Password" name="password"  
            rules={[
             {
              required:true,
              message:"Password required"
             
             },
             {min:4,message:"length must be 4 digit/characters"}
            
              ]} hasFeedback>
              <Input.Password type="password" autoComplete="off" />
            </Form.Item>

            <div className="flex justify-between items-center flex-col">
            <Link className="pb-4" to="/login">Click here to Login</Link>
            <button type="submit" className="secondary">Register</button>
          </div>
          </Form>
          
        </div>
      </div>
    </div>
  );
}

export default Register;
